import { Injectable, Optional, Inject, InjectionToken } from '@angular/core';

export const CardsManagementJourneyConfigurationToken =
  new InjectionToken<CardsManagementJourneyConfiguration>(
    'CardsManagementJourneyConfiguration injection token'
  );

export interface CardsManagementJourneyConfiguration {
  notificationTtl: number;
  groupByPaymentCardTypes: string;
  enableTravelNotice: boolean;
  deElevatedHeader: boolean;
}

const configDefaults: CardsManagementJourneyConfiguration = {
  notificationTtl: 5000,
  groupByPaymentCardTypes: 'Credit,Debit',
  enableTravelNotice: true,
  deElevatedHeader: false,
};

export const getDefaultConfigValues = (
  config: CardsManagementJourneyConfiguration
) => ({
  ...configDefaults,
  ...(config || {}),
});

@Injectable()
export class CardsManagementJourneyConfigService {
  private _config: CardsManagementJourneyConfiguration;

  constructor(
    @Optional()
    @Inject(CardsManagementJourneyConfigurationToken)
    config: CardsManagementJourneyConfiguration
  ) {
    this._config = getDefaultConfigValues(config);
  }

  get defaults(): CardsManagementJourneyConfiguration {
    return configDefaults;
  }

  /**
   * Notification timeout timer value configuration
   */
  get notificationTtl(): number {
    return this._config.notificationTtl;
  }

  /**
   * Types of cards configuration value
   */
  get groupByPaymentCardTypes(): string {
    return this._config.groupByPaymentCardTypes;
  }

  /**
   * Dev config to enable Travel notice
   */
  get enableTravelNotice(): boolean {
    return this._config.enableTravelNotice;
  }

  /**
   * Dev config to enable de-elevated header
   */
  get deElevatedHeader(): boolean {
    return this._config.deElevatedHeader;
  }
}
