import { CommonModule } from '@angular/common';
import { ModuleWithProviders, NgModule } from '@angular/core';
import { ReactiveFormsModule } from '@angular/forms';
import { provideRoutes, Route, RouterModule } from '@angular/router';
import { CardDetailsWidgetModule } from '@backbase/card-details-widget-ang';
import { CardsListWidgetModule } from '@backbase/cards-list-widget-ang';
import { CardsTravelNoticeModule } from '@backbase/cards-travel-notice-widget-ang';
import { BackbaseCoreModule } from '@backbase/foundation-ang/core';
import { HeaderModule } from '@backbase/ui-ang/header';
import { CardsManagementJourneyComponent } from './cards-management-journey.component';
import { CardsManagementTravelNoticeGuard } from './guard/cards-management-travel-notice.guard';
import { CardDetailViewComponent } from './views/card-detail-view.component';
import { CardsListViewComponent } from './views/cards-list-view.component';
import { CardsTravelNoticeViewComponent } from './views/cards-travel-notice-view.component';

export const defaultRoute: Route = {
  path: '',
  component: CardsManagementJourneyComponent,
  children: [
    {
      path: '',
      redirectTo: 'list',
      pathMatch: 'full',
    },
    {
      path: 'list',
      component: CardsListViewComponent,
    },
    {
      path: 'details',
      component: CardDetailViewComponent,
    },
    {
      path: 'travel-notice',
      component: CardsTravelNoticeViewComponent,
      canActivate: [CardsManagementTravelNoticeGuard],
    },
  ],
};

@NgModule({
  declarations: [
    CardsManagementJourneyComponent,
    CardsListViewComponent,
    CardDetailViewComponent,
    CardsTravelNoticeViewComponent,
  ],
  imports: [
    CommonModule,
    RouterModule,
    BackbaseCoreModule.withConfig({
      classMap: { CardsManagementJourneyComponent },
    }),
    CardsListWidgetModule,
    CardDetailsWidgetModule,
    CardsTravelNoticeModule,
    ReactiveFormsModule,
    HeaderModule,
  ],
  providers: [CardsManagementTravelNoticeGuard],
})
export class CardsManagementJourneyModule {
  static forRoot(
    data: { route: Route; [key: string]: any } = { route: defaultRoute }
  ): ModuleWithProviders<CardsManagementJourneyModule> {
    return {
      ngModule: CardsManagementJourneyModule,
      providers: [provideRoutes([data.route])],
    };
  }
}
